import * as React from "react";
import { initialMain3DViewState } from "./data/main3DViewState";
import { Size } from "../utility/useSize";
import * as R from "ramda";
import { useLoadFloaterModel } from "./useLoadFloaterModel";
import { usePointCamera } from "./usePointCamera";
import { useInitializeMain3DView } from "./useInitializeMain3DView";
import { IiotStoreState } from "../rootReducer";
import { FloaterString } from "./FloaterString";
import { useEffect } from "react";

export const Main3DViewContext = React.createContext(initialMain3DViewState);

export const Main3DView: React.FC<{
  size: Size;
  main3D: IiotStoreState["main3D"];
}> = props => {
  console.log("Main3DViewScene render");
  const main3DViewState = initialMain3DViewState;
  const floaterModel = useLoadFloaterModel();
  const stateReady = main3DViewState.selectionMesh !== undefined && floaterModel;
  useInitializeMain3DView();
  usePointCamera(props.main3D.installationStructure);
  useEffect(() => {
    main3DViewState.installationStructure = props.main3D.installationStructure;
  }, [props.main3D, main3DViewState]);
  return (
    <Main3DViewContext.Provider value={main3DViewState}>
      {stateReady &&
        main3DViewState.selectionMesh !== undefined &&
        props.main3D.installationStructure &&
        R.range(0, props.main3D.installationStructure.y).map(index => (
          <FloaterString index={index} key={index} />
        ))}
    </Main3DViewContext.Provider>
  );
};
