import * as React from "react";
import { makeStyles, Theme } from "@material-ui/core/styles";
import { Typography } from "@material-ui/core";
import { useCallback, useMemo, useState } from "react";
import { main3DViewActions } from "../../3d/data/main3DViewReducer";
import { useDispatch } from "react-redux";

export const ActionsAlerts: React.FC<{}> = () => {
  const styles = withStyles();
  const dispatch = useDispatch();
  const selectHF09 = useCallback(
    () => dispatch(main3DViewActions.setSelectedCoordinate({ x: 1, y: 2 })),
    [dispatch]
  );
  const selectHF12 = useCallback(
    () => dispatch(main3DViewActions.setSelectedCoordinate({ x: 0, y: 3 })),
    [dispatch]
  );
  const selectHF14 = useCallback(
    () => dispatch(main3DViewActions.setSelectedCoordinate({ x: 2, y: 3 })),
    [dispatch]
  );

  const [dismissedMessages, setDismissedMessages] = useState<number[]>([]);

  const messages: {
    message: string;
    id: number;
    onClick(): void;
    onClickName: string;
  }[] = useMemo(
    () => [
      {
        message: "High tension detected in HF12",
        onClickName: "view HF12",
        onClick: selectHF12,
        id: 0
      },
      {
        message: "Floater HF09 approaches end of life",
        onClickName: "view HF09",
        onClick: selectHF09,
        id: 1
      },
      {
        message: "Floater HF14 approaches end of life",
        onClickName: "view HF14",
        onClick: selectHF14,
        id: 2
      },
    ],
    [selectHF12, selectHF09, selectHF14]
  );

  return (
    <div className={styles.root}>
      <div className={styles.titleArea}>
        <Typography variant={"h4"} classes={{ root: styles.heading }}>
          Actions / Alerts
        </Typography>
      </div>
      <div className={styles.content}>
        {messages.map(messageDef =>
          dismissedMessages.includes(messageDef.id) ? null : (
            <div className={styles.eventRow} key={messageDef.id}>
              <Typography
                variant={"body1"}
                classes={{ root: styles.inlineText }}
              >
                {messageDef.message}
              </Typography>
              <Typography
                variant={"body1"}
                classes={{ root: styles.eventRowLink }}
                onClick={messageDef.onClick}
              >
                {messageDef.onClickName}
              </Typography>
              <Typography
                variant={"body1"}
                classes={{ root: styles.eventRowLink }}
                onClick={() =>
                  setDismissedMessages([...dismissedMessages, messageDef.id])
                }
              >
                dismiss
              </Typography>
            </div>
          )
        )}
      </div>
    </div>
  );
};

const withStyles = makeStyles((theme: Theme) => ({
  root: {
    position: "absolute",
    bottom: 0,
    right: 0,
    width: "600px",
    height: "275px",
    // todo delete
    padding: "16px",
    pointerEvents: "all",
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
    borderRadius: "4px",
    boxShadow:
      "0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12)",

    overflow: 'hidden',
  },
  heading: {
    textAlign: "center"
  },
  titleArea: {
    display: "flex",
    justifyContent: "space-around",
    marginLeft: "-16px",
    marginTop: "-16px",
    marginRight: "-16px",
    padding: "16px",
    backgroundColor: theme.palette.secondary.main,
    color: theme.palette.secondary.contrastText,
    boxSizing: 'border-box',
    height: "73px",
  },
  content: {
    marginTop: "20px",
    marginLeft: "50px"
  },
  inlineText: {
    display: "inline"
  },
  eventRowLink: {
    textDecoration: "underline",
    cursor: "pointer",
    marginLeft: "16px"
  },
  eventRow: {
    display: "flex"
  }
  // grid: {
  //   display: "grid",
  //   gridTemplateColumns: "1fr 2fr",
  //   marginTop: "20px",
  //   marginLeft: "50px"
  // },
  // error: {
  //   textDecoration: "overline underline"
  // }
}));
