import * as React from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { Provider } from "react-redux";
import { store } from "./store";
import { MuiPickersUtilsProvider } from "@material-ui/pickers";
import DayjsUtils from "@date-io/dayjs";
import "dayjs/locale/nb";
import { createMuiTheme } from "@material-ui/core";
import ThemeProvider from "@material-ui/styles/ThemeProvider";
import { useRef } from "react";
import { setupDatGui, DatGuiContext } from "./gui/datGui";

const theme = createMuiTheme({
  palette: {
    primary: { main: "#1070E4" },
    secondary: { main: "#FFEA00" }
  }
});

export const IiotContextProviders: React.FC<{
  children: React.ReactNode;
}> = props => {
  const datGuiRef = useRef(setupDatGui());
  return (
    <Provider store={store}>
      <Router basename={process.env.PUBLIC_URL}>
        <MuiPickersUtilsProvider utils={DayjsUtils}>
          <ThemeProvider theme={theme}>
            <DatGuiContext.Provider value={datGuiRef.current}>
              {props.children}
            </DatGuiContext.Provider>
          </ThemeProvider>
        </MuiPickersUtilsProvider>
      </Router>
    </Provider>
  );
};
