import { useContext, useEffect } from "react";
import * as THREE from "three";
import { Main3DViewContext } from "./Main3DView";

export const useInitializeObjects = () => {
  const state = useContext(Main3DViewContext);
  useEffect(() => {
    const geo = new THREE.BoxGeometry(3.2, 0.3, 3.2);
    const mat = new THREE.MeshPhongMaterial({
      color: 0x1070e4,
      opacity: 0.5,
      transparent: true
    });
    state.selectionMesh = new THREE.Mesh(geo, mat);
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
};
