import * as React from "react";
import { makeStyles, Theme } from "@material-ui/core/styles";
import { IconButton } from "@material-ui/core";
import { InfoPanelDialog } from "./InfoPanelDialog";
import { useCallback, useState } from "react";

enum DIALOGS {
  BIRD_LASER,
  POWER_METER,
  SUN_OBSTRUCTION,
  WEATHER,
  MAINTENANCE
}

export const InfoPanel: React.FC<{}> = () => {
  const [openDialog, setOpenDialog] = useState<number | undefined>(undefined);
  const styles = useStyles();
  return (
    <>
      <div className={styles.root}>
        <IconButton
          classes={{ root: "x" }}
          onClick={useCallback(() => setOpenDialog(DIALOGS.BIRD_LASER), [
            setOpenDialog
          ])}
        >
          <img
            src={process.env.PUBLIC_URL + "/info-panel/bird-with-laser.png"}
            alt={"Bird laser"}
          />
        </IconButton>
        <IconButton
          classes={{ root: "x" }}
          onClick={useCallback(() => setOpenDialog(DIALOGS.POWER_METER), [
            setOpenDialog
          ])}
        >
          <img
            src={process.env.PUBLIC_URL + "/info-panel/power-meter.png"}
            alt={"Power meter"}
          />
        </IconButton>
        <IconButton
          classes={{ root: "x" }}
          onClick={useCallback(() => setOpenDialog(DIALOGS.SUN_OBSTRUCTION), [
            setOpenDialog
          ])}
        >
          <img
            src={process.env.PUBLIC_URL + "/info-panel/cell-cover.png"}
            alt={"Sun obstructions"}
          />
        </IconButton>
        <IconButton
          classes={{ root: "x" }}
          onClick={useCallback(() => setOpenDialog(DIALOGS.WEATHER), [
            setOpenDialog
          ])}
        >
          <img
            src={process.env.PUBLIC_URL + "/info-panel/weather.png"}
            alt={"Weather"}
          />
        </IconButton>
        <IconButton
          classes={{ root: "x" }}
          onClick={useCallback(() => setOpenDialog(DIALOGS.MAINTENANCE), [
            setOpenDialog
          ])}
        >
          <img
            src={process.env.PUBLIC_URL + "/info-panel/cleaning.png"}
            alt={"Maintenance schedule"}
          />
        </IconButton>
      </div>
      <InfoPanelDialog
        open={openDialog === DIALOGS.BIRD_LASER}
        onClose={useCallback(() => setOpenDialog(undefined), [setOpenDialog])}
        title={"Bird Laser"}
      >
        <img
          src={process.env.PUBLIC_URL + "/res/corssed_square.png"}
          alt={"placeholder element"}
        />
      </InfoPanelDialog>
      <InfoPanelDialog
        open={openDialog === DIALOGS.POWER_METER}
        onClose={useCallback(() => setOpenDialog(undefined), [setOpenDialog])}
        title={"Power Meter"}
      >
        <img
          src={process.env.PUBLIC_URL + "/res/corssed_square.png"}
          alt={"placeholder element"}
        />
      </InfoPanelDialog>
      <InfoPanelDialog
        open={openDialog === DIALOGS.SUN_OBSTRUCTION}
        onClose={useCallback(() => setOpenDialog(undefined), [setOpenDialog])}
        title={"Sun obstruction"}
      >
        <img
          src={process.env.PUBLIC_URL + "/res/corssed_square.png"}
          alt={"placeholder element"}
        />
      </InfoPanelDialog>
      <InfoPanelDialog
        open={openDialog === DIALOGS.WEATHER}
        onClose={useCallback(() => setOpenDialog(undefined), [setOpenDialog])}
        title={"Weather"}
      >
        <img
          src={process.env.PUBLIC_URL + "/res/corssed_square.png"}
          alt={"placeholder element"}
        />
      </InfoPanelDialog>
      <InfoPanelDialog
        open={openDialog === DIALOGS.MAINTENANCE}
        onClose={useCallback(() => setOpenDialog(undefined), [setOpenDialog])}
        title={"Maintenance"}
      >
        <img
          src={process.env.PUBLIC_URL + "/res/corssed_square.png"}
          alt={"placeholder element"}
        />
      </InfoPanelDialog>
    </>
  );
};

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    position: "absolute",
    top: "116px",
    left: 0,
    width: "100px",
    height: "450px",
    pointerEvents: "all",
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
    boxShadow:
      "0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12)",
    borderRadius: "4px"
  }
}));
