import { createAction, createReducer } from "typesafe-actions";
import { ActionType } from "typesafe-actions/dist/type-helpers";

export const guiActions = {
  setGuiVisible: createAction("GUI/SET_VISIBLE", (visible: boolean) => ({
    visible: visible
  }))()
};

interface GuiState {
  visible: boolean;
}

export type GuiAction = ActionType<typeof guiActions>;

export const guiReducer = createReducer<GuiState, GuiAction>({
  visible: true
}).handleAction(guiActions.setGuiVisible, (state, action) => ({
  ...state,
  visible: action.payload.visible
}));
