export const Floater: React.FC<{
  position: [number, number, number];
  motionPeriod: number;
  onClick?: () => void;
  selected: boolean;
}> = props => {
  const state = useContext(Main3DViewContext);
  const group = useMemo(() => {
    if (state.floaterMesh === undefined) {
      throw new Error("Floater model is not ready");
    }
    const newMesh = state.floaterMesh.clone();
    const group = new THREE.Group();
    group.add(newMesh);
    return group;
  }, [state.floaterMesh]);
  const motionEnabled = useSelector(
    (state: IiotStoreState) => state.main3D.motionEnabled
  );

  // this hack fixes a problem with "resetting" spring
  // https://github.com/react-spring/react-spring/issues/366#issuecomment-446328238
  const [springState, setSpringState] = useState({
    pos: props.position,
    from: {
      pos: props.position
    },
    config: sprindDurationConfigWithPeriod(props.motionPeriod)
  });
  const spring = useSpring({
    ...springState
  });
  const previousProps = usePrevious(props);
  useEffect(() => {
    if (motionEnabled) {
      setSpringState({
        pos: props.position,
        from: {
          pos:
            previousProps !== undefined
              ? previousProps.position
              : props.position
        },
        config: sprindDurationConfigWithPeriod(props.motionPeriod)
      });
    }
  }, [props.position, props.motionPeriod]); // eslint-disable-line react-hooks/exhaustive-deps

  const selectionRef = useRef<any>();
  useEffect(() => {
    if (props.selected) {
      selectionRef.current = state.selectionMesh;
      group.add(selectionRef.current);
    } else {
      group.remove(selectionRef.current as any);
    }
  }, [props.selected, group, state.selectionMesh]);

  return useMemo(
    () =>
      group ? (
        <>
          <animated.primitive
            onClick={props.onClick}
            object={group}
            position={spring.pos}
          />
        </>
      ) : null,
    [spring.pos, group, props.onClick]
  );
};
