import * as React from "react";
import { makeGuiReduxSwitch } from "../utility/useDatGuiReduxSwitch";
import { IiotStoreState } from "../rootReducer";
import { guiActions } from "./guiReducer";

const useGuiSwitch = makeGuiReduxSwitch({
  guiName: "GUI visible",
  selector: (state: IiotStoreState) => state.gui.visible,
  updateAction: guiActions.setGuiVisible
});

export const GuiWrapper: React.FC<{ children: React.ReactNode }> = props => {
  useGuiSwitch();
  return <>{props.children}</>;
};
