import { AnimationMixer, Mesh } from "three";

export interface Main3DViewState {
  /**
   * Mixers to update in animation loop
   */
  _mixers: AnimationMixer[];
  /**
   * Floater mesh to reuse
   */
  floaterMesh: Mesh | undefined;
  selectionMesh: Mesh | undefined;
  installationStructure: { x: number; y: number };
}

export const initialMain3DViewState: Main3DViewState = {
  _mixers: [],
  floaterMesh: undefined,
  gridDimensions: { x: 0, y: 0 }
} as any;
