import { ActionType, createAction, createReducer } from "typesafe-actions";
import { IiotAction } from "../../rootReducer";
import { InstallationStructure } from "../utility/installationStructure";
import { TwoDCoord } from "../../utility/types";

interface Main3DViewState {
  installationStructure: InstallationStructure;
  selectedFloaterCoordinate: TwoDCoord | null;
  motionEnabled: boolean;
}

const initialMain3DViewState: Main3DViewState = ({
  installationStructure: undefined,
  selectedFloaterCoordinate: null,
  motionEnabled: true
} as Partial<Main3DViewState>) as Main3DViewState;

export const main3DViewActions = {
  setInstallationStructure: createAction(
    "SET_INSTALLATION_STRUCTURE",
    (structure: InstallationStructure) => ({ structure })
  )(),
  setSelectedCoordinate: createAction(
    "MAIN_3D_VIEW/SET_SELECTED_FLOATER_COORDINATE",
    (coordinate: TwoDCoord) => ({ coordinate })
  )(),
  selectNextFloater: createAction("MAIN_3D_VIEW/SELECT_NEXT_FLOATER")(),
  selectPreviousFloater: createAction("MAIN_3D_VIEW/SELECT_PREVIOUS_FLOATER")(),
  clearSelectedCoordinate: createAction(
    "MAIN_3D_VIEW/CLEAR_SELECTED_FLAOTER_COORDINATE"
  )(),
  updateMotionEnabled: createAction(
    "MAIN_3D_VIEW/UPDATE_MOTION_ENABLED",
    (motionEnabled: boolean) => ({ motionEnabled })
  )()
};

export type Floaters3DViewAction = ActionType<typeof main3DViewActions>;

export const main3DViewReducer = createReducer<Main3DViewState, IiotAction>(
  initialMain3DViewState
)
  .handleAction(
    main3DViewActions.setInstallationStructure,
    (state, action) => ({
      ...state,
      installationStructure: action.payload.structure
    })
  )
  .handleAction(main3DViewActions.setSelectedCoordinate, (state, action) => ({
    ...state,
    selectedFloaterCoordinate: action.payload.coordinate
  }))
  .handleAction(main3DViewActions.clearSelectedCoordinate, state => ({
    ...state,
    selectedFloaterCoordinate: null
  }))
  .handleAction(main3DViewActions.selectNextFloater, state => ({
    ...state,
    selectedFloaterCoordinate: nextFloater(state.selectedFloaterCoordinate)
  }))
  .handleAction(main3DViewActions.selectPreviousFloater, state => ({
    ...state,
    selectedFloaterCoordinate: previous(state.selectedFloaterCoordinate)
  }))
  .handleAction(main3DViewActions.updateMotionEnabled, (state, action) => ({
    ...state,
    motionEnabled: action.payload.motionEnabled
  }));

function nextFloater(coord: TwoDCoord | null) {
  if (coord === null) {
    return null;
  }
  if (coord.x === 3 && coord.y === 4) {
    return coord;
  }
  if (coord.x < 3) {
    return { x: coord.x + 1, y: coord.y };
  } else {
    return { x: 0, y: coord.y + 1 };
  }
}

function previous(coord: TwoDCoord | null) {
  if (coord === null) {
    return null;
  }
  if (coord.x === 0 && coord.y === 0) {
    return coord;
  }
  if (coord.x > 0) {
    return { x: coord.x - 1, y: coord.y };
  } else {
    return { x: 3, y: coord.y - 1 };
  }
}
