import { Floaters3DViewAction, main3DViewReducer } from "./3d/data/main3DViewReducer";
import { combineReducers } from "redux";
import { GuiAction, guiReducer } from "./gui/guiReducer";

export const rootReducer = combineReducers({
  main3D: main3DViewReducer,
  gui: guiReducer
});

export type IiotStoreState = ReturnType<typeof rootReducer>;

export type IiotAction = Floaters3DViewAction | GuiAction;
