import * as React from "react";
import { makeStyles, Theme } from "@material-ui/core/styles";
import ReactResizeDetector from "react-resize-detector";
import { useSize } from "../utility/useSize";
import { useEffect } from "react";
import { IiotStoreState } from "../rootReducer";
import {
  useDispatch,
  useSelector,
  ReactReduxContext,
  Provider
} from "react-redux";
import { Canvas } from "react-three-fiber";
import { Main3DView } from "./Main3DView";
import { retrieveInstallationStructureThunk } from "./data/retrieveInstallationStructureThunk";
import { makeGuiReduxSwitch } from '../utility/useDatGuiReduxSwitch';
import { main3DViewActions } from './data/main3DViewReducer';

const withStyles = makeStyles((theme: Theme) => ({
  mainContainer: {
    position: "absolute",
    top: "0",
    zIndex: 1,
    left: "0",
    width: "100vw",
    height: "100vh"
  },
  canvas: {}
}));

const useGuiSwitch = makeGuiReduxSwitch({
  guiName: "Motion enabled",
  selector: (state: IiotStoreState) => state.main3D.motionEnabled,
  updateAction: main3DViewActions.updateMotionEnabled,
});

export const Main3DViewContainer: React.FC = () => {
  const styles = withStyles();
  const [size, setSize] = useSize();
  const main3DView = useSelector((state: IiotStoreState) => state.main3D);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(retrieveInstallationStructureThunk());
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  useGuiSwitch();

  return (
    <div className={styles.mainContainer}>
      <ReactResizeDetector handleHeight handleWidth onResize={setSize} />
      {/*Hack to fix some bug that destroys context*/}
      {/*https://github.com/react-spring/react-three-fiber/issues/43#issuecomment-476073615*/}
      <ReactReduxContext.Consumer>
        {({ store }) => (
          <Canvas className={styles.canvas} resize={{ scroll: false }}>
            <Provider store={store}>
              {main3DView.installationStructure && (
                <Main3DView size={size} main3D={main3DView} />
              )}
            </Provider>
          </Canvas>
        )}
      </ReactReduxContext.Consumer>
    </div>
  );
};
