import * as React from "react";
import { DumbOverlayContainer } from "./DumbOverlayContainer";
import { SelectedInfo } from "./windows/SelectedInfo";
import { TopBar } from "./windows/TopBar";
import { InfoPanel } from "./windows/InfoPanel";
import { ActionsAlerts } from "./windows/ActionsAlerts";
import { IiotStoreState } from "../rootReducer";
import { useSelector } from "react-redux";

export const Overlay: React.FC = () => {
  const guiVisible = useSelector((state: IiotStoreState) => state.gui.visible);
  return (
    <>
      {guiVisible && (
        <DumbOverlayContainer>
          <InfoPanel />
          <TopBar />
          <SelectedInfo />
          <ActionsAlerts />
        </DumbOverlayContainer>
      )}
    </>
  );
};
