import React from "react";
import "./App.css";
import { Switch, Route, Redirect } from "react-router-dom";
import { IiotContextProviders } from "./IiotAppProviders";
import { Main3DViewContainer } from "./3d/Main3DViewContainer";
import { Overlay } from "./overlay/Overlay";
import { GuiWrapper } from "./gui/GuiWrapper";

function App() {
  return (
    <IiotContextProviders>
      <GuiWrapper>
        <Switch>
          <Redirect exact from="/" to={"/dash"} />
          <Route path="/dash">
            <Main3DViewContainer />
            <Overlay />
          </Route>
        </Switch>
      </GuiWrapper>
    </IiotContextProviders>
  );
}

export default App;
