import { IiotStoreState } from "../rootReducer";
import { useDispatch, useSelector } from "react-redux";
import { useContext, useEffect, useMemo, useRef } from "react";
import { DatGuiContext } from "../gui/datGui";
import { GUIController } from 'dat.gui';

interface Params<T> {
  updateAction: (value: T) => void;
  selector: (state: IiotStoreState) => T;
  guiName: string;
}

export const makeGuiReduxSwitch = <T>(params: Params<T>) => () => {
  const dispatch = useDispatch();
  const currentValue = useSelector(params.selector);
  const datGui = useContext(DatGuiContext);
  const guiControlRef = useRef<GUIController>();

  const hiddenPropertyObject = useMemo(() => {
    return {
      _property: currentValue,
      get property() {
        return this._property;
      },
      set property(property: T) {
        dispatch(params.updateAction(property));
        this._property = property;
      }
    };
  }, [dispatch]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    const elem = datGui.add(hiddenPropertyObject, "property");
    elem.name(params.guiName);
    guiControlRef.current = elem;
    return () => datGui.remove(elem);
  }, [hiddenPropertyObject, datGui]);
};
